import { elements } from './base';

export const getInput = ()=> elements.searchInput.value;

export const clearInput = () =>{
    elements.searchInput.value ='';

} 

export const clearUIResult = () => {
    elements.searchResList.innerHTML = '';
    elements.searchResPages.innerHTML = '';
}

export const highlightSelected = id => {
    const resultsArr = Array.from(document.querySelectorAll('.results__link'))
    // 1)remove selected style
    resultsArr.forEach(el => {
        el.classList.remove('results__link--active');
    })
    // 2) add selected style
    const element =  document.querySelector(`.results__link[href*="${id}"]`);
    if (element) element.classList.add('results__link--active');
    // document.querySelector(`a[href*="${id}"]`).classList.add('results__link--active')
    // classList.add('results__link--active');
    // console.log(r);
}


export const limitRecipeTitle = (title, limit = 17) => {
    const newTitle = [];
    if (title.length > limit) {
        title.split(' ').reduce( (acc,cur) => {
            if (acc + cur.length <= limit) {
                newTitle.push(cur);
            }
            return acc + cur.length 
        },0);
        return `${newTitle.join(' ')} ...`;
    }
    return title;
}


const renderRecipe = recipe => {
    const markup = `
    <li>
    <a class="results__link" href="#${recipe.recipe_id}">
        <figure class="results__fig">
            <img src="${recipe.image_url}" alt="${recipe.title}">
        </figure>
        <div class="results__data">
            <h4 class="results__name">${limitRecipeTitle(recipe.title)}</h4>
            <p class="results__author">${recipe.publisher}</p>
        </div>
    </a>
    </li>
    `;
    elements.searchResList.insertAdjacentHTML('beforeend',markup);
}

const createButton = (page,type) => `
    <button class="btn-inline results__btn--${type}" data-goto= ${type === 'prev' ? page-1 : page + 1 }>
    <span>Page ${type === 'prev' ? page-1 : page + 1 }</span>
    <svg class="search__icon">
        <use href="img/icons.svg#icon-triangle-${type === 'prev' ? 'left' : 'right'}"></use>
    </svg>
    
    </button>
`;

const renderButtons = (page,numResults,resPerPage) => {
    const pages = Math.ceil(numResults/resPerPage);
    let button;
    if (page === 1 && pages >1) {
        //one button to go to the next pages
        button = createButton(page,'next');
    } else if (page === pages && pages >1) {
        //one button on left to go to previous pages
        button = createButton(page,'prev');
    } else if(page < pages){
        // two buttons on each side, forward and back
        button = `
            ${createButton(page,'prev')}
            ${createButton(page,'next')}
            `;
    }
    elements.searchResPages.insertAdjacentHTML('afterbegin', button);
};

export const renderResults = (recipes, page = 1,resPerPage = 5) => {
    // manage the page and result each page
    const start = (page - 1) * resPerPage;
    const end = page * resPerPage;
    // take part of the recipes array to display
    recipes.slice(start,end).forEach(renderRecipe);
    // render pagination button
    renderButtons(page,recipes.length,resPerPage);
}

